import {action, makeObservable, observable} from "mobx";
import {BaseInfoType} from "../../DesignerType";
import EditorDesignerLoader from "../../loader/EditorDesignerLoader";

/**
 * 
 */
class CompListStore {
    constructor() {
        makeObservable(this, {
            compKey: observable,
            visible: observable,
            setVisible: action,
            setCompKey: action,
        });
    }

    /**
     * 
     *     
     */
    compInfoArr: Array<BaseInfoType> = [];
    /**
     * 
     */
    compKey: string = '';
    /**
     * 
     */
    visible: boolean = false;

    doInit = () => {
        const comps: Array<any> = [];
        const {customComponentInfoMap} = EditorDesignerLoader.getInstance();
        if (customComponentInfoMap) {
            Object.keys(customComponentInfoMap).forEach(key => {
                let baseInfo = customComponentInfoMap[key].getBaseInfo();
                if (baseInfo.compKey !== 'LcBg') comps.push(baseInfo);
            });
        }
        this.compInfoArr = comps;
    }

    setVisible = (visible: boolean) => this.visible = visible;

    setCompKey = (key: string) => this.compKey = key;
}

const compListStore = new CompListStore();
export default compListStore;